				item.style.backgroundColor = 'rgba(0,0,0,.7)'
			})
			if (scrollTop >= activityTop) {
				this.searchActive = true
				var list = document.querySelector('.header-bg1')
				document.querySelector('.mul-left').classList.add('mul-left1')
				if(list !== null) {
					document.querySelector('.header-bg1').classList.add('header-bg2')
				}
				if(scrollTop <= scrollList[0]) {
					mulLeftList[1].style.backgroundColor = scrollColor[0]
				} else if (scrollTop <= scrollList[1]) {
					mulLeftList[2].style.backgroundColor = scrollColor[1]
				} else if (scrollTop <= scrollList[2]) {
					mulLeftList[3].style.backgroundColor = scrollColor[2]
				} else if (scrollTop <= scrollList[3]) {
					mulLeftList[4].style.backgroundColor = scrollColor[3]
				} else if (scrollTop <= scrollList[4]) {
					mulLeftList[5].style.backgroundColor = scrollColor[4]
				} else if (scrollTop <= scrollList[5]) {
					mulLeftList[6].style.backgroundColor = scrollColor[5]
				} else if (scrollTop <= scrollList[6]) {
					mulLeftList[7].style.backgroundColor = scrollColor[6]
				} else {
					mulLeftList[8].style.backgroundColor = scrollColor[7]
				}
			} else {
				var list = document.querySelector('.header-bg2')
				var list1 = document.querySelector('.mul-left1')
				if(list) {
					document.querySelector('.header-bg1').classList.remove('header-bg2')
				}
				if(list1) {
					document.querySelector('.mul-left').classList.remove('mul-left1')
				}
				this.searchActive = false
			}
		},
		// 置顶
		backTop () {
			var that = document.documentElement.scrollTop
			var list = setInterval(function () {
				document.documentElement.scrollTop = that
				that -= 100
				if(that <= -100) {
					clearInterval(list)
				}
			},18)
		},
		topClick () {
			var lists = document.querySelector('.mul-left').children[1].children
			// var scrollList = document.querySelectorAll('.scroll-type')
			var scrollList = [1010,1750,2520,3210,4000,4680,5450,6135]
			Object.values(lists).forEach((item,index) => {
				if (index === 8) return false
				item.addEventListener('click',function () {
					var end = document.documentElement.scrollTop
					var that = document.documentElement.scrollTop
					var list = setInterval(function () {
						if (end >= scrollList[index]) {
							document.documentElement.scrollTop = that
							that -= 100
							if(that <= scrollList[index] - 100) {
								clearInterval(list)
							}
						} else {
							document.documentElement.scrollTop = that
							that += 100
							if(that >= scrollList[index]) {
								clearInterval(list)
							}
						}
					},18)
				})
			})
		},
		// 获取商品分类
		async getIndexList () {
			const res = await this.$http.get('normal')
			console.log(res.data)
			this.normalNavList = res.data.data
			// console.log(Img)
		},
		// 获取brand 数据
		getBrandList () {
			var listText = [
				['优惠券 ￥550','同仁堂','优惠券 ￥140','Schneider Electric/施耐德','ENFINITAS/蓝臻','Babyzen','Siwy','WILO/威乐','优惠券 ￥100','胜利'],
				['LEAGUE OF LEGENDS/英雄联盟','上汽大众','Modern HOUSE','Schneider Electric/THE NORTH FACE/北面','优惠券 ￥400','Effectim/玑妍之光','Neato Robotics','Teddy Bear/泰迪熊','优惠券 ￥1000','优惠券 ￥80'],
				['帕尔玛之水','优惠券 ￥400','优惠券 ￥400','优惠券 ￥400','DAVINES/大卫尼斯','Lightstim','德克士','优惠券 ￥550','STOKKE','']
			]
			var list = {}
			const context = require.context('../assets', true, /\.png$/); // 根据路径正则读取文件
			for (let j = 0; j < 3; j++) {
				for (let k = 0; k < 10; k++) {
					if(j === 2 && k === 9) continue
					let imgName = `./index03-0${j + 1}-0${k + 1}.png`;
					list.imgUrl = context(imgName)
					list.text = listText[j][k]
					this.brandImg.push(list)
					list = {}
				}
			}
		},
		// 获取 shop组件 数据
		async getShopList () {
			const res = await this.$http.get('shop')
			console.log(res.data.data)
			this.shopList = res.data.data
			const context = require.context('../assets', true, /\.jpg$/);
			this.shopList.forEach((item) => {
				item.topImg = context(item.topImg)
				item.lists.forEach((i) => {
					i.leftImg = context(i.leftImg)
					i.list.forEach((res) => {
						res.img = context(res.img)
					})
				})
			})
		},
		// 获取shopList 组件 数据
		async getShopListC () {
			const res = await this.$http.get('list')
			// console.log(res.data.data)
			this.shopListC = res.data.data
			const context = require.context('../assets', true, /\.png$/);
			this.shopListC.forEach((item) => {
				item.img = context(item.img)
			})
		},
		carousel () {
			// console.log(this.$refs.elcarousel.activeIndex)
			if (this.$refs.elcarousel.activeIndex !== 0){
				this.normalLunActive = false
			} else {
				this.normalLunActive = true
			}
		},
		navItems (e) {
			// console.log(e)
			// 修改颜色
			this.colorCl = `line-con-color${e + 1}`
			
			this.pannelConActiveIndex = e
			this.pannelConActiveList = Object.values(this.$refs.navItem[e].children)
			// 清空选中样式
			var lists = document.querySelectorAll('.nav-item-bg')
			var lists1 = document.querySelectorAll('.nav-item-color')
			if(lists.length !== 0){
				Object.values(lists).forEach((item) => {
					item.classList.remove('nav-item-bg')
				})
				// Object.values(lists1).forEach((item) => {
				// 	item.classList.remove('nav-item-color')
				// })
			}
			this.$refs.navItem[e].classList.add('nav-item-bg')
			// this.pannelConActiveList.forEach((item) => {
			// 	item.classList.add('nav-item-color')
			// })
			this.pannelConActive = true
		},
		navItem2 () {
			this.$refs.navItem[this.pannelConActiveIndex].classList.remove('nav-item-bg')
			// this.pannelConActiveList.forEach((item) => {
			// 	item.classList.remove('nav-item-color')
			// })
			this.pannelConActive = false
		},
		// 切换品牌
		refreshBtn(){
			var delect = document.querySelectorAll('.brand-item1')
			var lists = document.querySelectorAll('.brand-item')
			if(delect.length !== 0){
				lists.forEach((item) => {
					item.classList.remove('brand-item1')
				})
			}
			setTimeout(function(){
				lists.forEach((item) => {
					item.classList.add('brand-item1')
				})
			},1)
		}
	}
}
